.habilidades-container{
    height: 100vh;
    margin: 20px;
    flex-direction: column;  
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: space-around;
    background-color: #a54657;
}

.barrinha-container{
    border: 2px solid white;
    flex: 1;
    border-radius: 30px;
    flex-direction: row;
    display: flex;
}

.habilidade-container{
    display: flex;
    flex-direction: row;
    width: 100%;
    
}
.preenchimento-barrinha{
    background-color: white;
    border-radius: 30px;
}

.margem-habilidade{
    margin:5px;
}